/*-------- Search ---------*/

.search-element {
	position: relative;
}

.header .form-inline {
	.form-control {
		border: 0px solid #f6f6f6;
		padding-left: 55px;
		padding-right: 40px;
		background: $white;
		border-radius: 10px;
		&:focus {
			box-shadow: none;
		}
		&::-webkit-input-placeholder {
			font-size: 0.9375rem;
			opacity: 0.5;
		}
	}

	.btn {
		border-radius: 0;
		padding: 0px 15px 0px 15px;
		border: 1px solid transparent;
		box-shadow: none;
		border-bottom-right-radius: 4px;
		border-top-right-radius: 4px;
		background: transparent;
		color: $white;
		font-size: 15px;
		position: absolute;
		left: 0;
		opacity: 0.5;
		top: 4px;

		i {
			color: #818c9b;
		}
	}
}

@media (max-width: 767) {
	.header .form-inline .search-element {
		position: absolute;
		top: -4px !important;
		left: 10px;
		right: 10px;
		z-index: 892;
		display: none;
		margin-left: 0;
	}

	body.search-show .header .form-inline .search-element {
		display: block;
	}

	.header .navsearch i {
		font-size: 20px;
		color: $white;
	}

	.header {
		.form-inline .form-control::-webkit-input-placeholder {
			color: $color;
		}

		.navsearch i {
			font-size: 20px;
			color: $color;
			padding-top: 5px;
		}
	}
}

@media (max-width: 320px) {
	.desktop-lgo {
		display: inline-block !important;
		margin-top: 3px !important;
	}
}

@media (max-width: 1024px) {
	.app.sidebar-gone.search-show .header.top-header {
		z-index: 1036;
	}

	body {
		&.sidebar-show {
			overflow: hidden;
		}

		&.search-show:before,
		&.sidebar-show:before {
			content: "";
			position: fixed;
			left: 0;
			right: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(0, 0, 0, 0.4);
			opacity: 0.6;
			z-index: 891;
		}
	}
}

@media (min-width: 575.98px) and (max-width: 1200px) {
	.header .form-inline {
		.form-control {
			width: 100% !important;
			min-width: 100% !important;
		}
	}
}

@media (min-width: 767.98px) {
	.responsive-search {
		display: none !important;
	}
}

@media (max-width: 991px) and (min-width: 767px) {
	.animated-arrow.hor-toggle span {
		margin-left: 8px;
		margin-top: 2px;
		vertical-align: middle;
	}
}

@media (max-width: 991px) {
	.hor-header {
		box-shadow: 16px 0px 16px rgba(24, 23, 38, 0.06);
		border-bottom: 1px solid transparent !important;
	}
}

@media (max-width: 575.98px) {
	body.search-show .header .form-inline .search-element {
		display: block;
	}

	.header .form-inline .search-element .form-control {
		float: left;
		width: 100% !important;
		height: 40px;
		margin: 0 auto;
		background: $white;
		border-radius: 4px;
	}
	.form-control.header-search {
		color: $black;
	}

	.header {
		.form-inline {
			.search-element {
				position: absolute;
				z-index: 892;
				display: none;
			}

			.btn {
				color: #46494a !important;
			}

			.form-control::-webkit-input-placeholder {
				color: $color !important;
			}
		}

		.navsearch i {
			font-size: 20px;
			color: $color;
			padding-top: 4px;
		}
	}

	.hor-header .header-brand-img {
		&.desktop-lgo {
			display: none;
		}
	}
}

