// Toast styles //

.toast {
    max-width: 100%;
    width: 300px;
    overflow: hidden;
    font-size: 0.875rem;
    background-color: rgba(255, 255, 255, 0.85);
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, 0.1);
    box-shadow: 0 0.25rem 0.75rem rgba(0, 0, 0, 0.10) !important;
    backdrop-filter: blur(10px);
    opacity: 0;
    border-radius: 0.25rem;
}
.toast-header {
    display: flex;
    align-items: center;
    padding: 0.55rem 0.75rem;
    background-color: rgba(255, 255, 255, 0.85);
    background-clip: padding-box;
    border-bottom: 1px solid #e6ebf1;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}
.toast-header .btn-close {
    font-weight: 500;
    margin-top: 0px;
    margin-right: 0px;
	font-size: 20px;
}
.toast-body {
    padding: 1rem;
}
.toast-header.bg-primary {
	color: $white !important;
}
.toast-header h6 {
	margin-top: 2px;
}
.demo-static-toast .toast {
    opacity: 1;
}

.toast.show {
    display: block;
}
.toast:not(:last-child) {
    margin-bottom: 0.75rem;
}

.toast-main {
    padding: 1rem;
    border: 1px solid #f0f2f8;
    font-size: 0.9375rem;
    border-radius: 11px;
}

.toast-main {
	.r-10 {
		right: 10px !important;
	}
	.t-10 {
		top: 10px !important;
	}
}

// dark-styles for toast

.dark-mode {
	.toast {
		background-color: rgba(0,0,0, 0.85);
		border: 1px solid rgba(255,255,255, 0.1);
		box-shadow: 0 0.25rem 0.75rem rgba(255,255,255, 0.10) !important;
	}
	.toast-header {
		background-color: rgba(255,255,255,0.05);
		border-bottom: 1px solid rgba(255,255,255,0.1);
	}
	.toast-main {
		border: 1px solid rgba(255,255,255,0.1);
	}
	.toast-header .btn-close {
		font-weight: 500;
		margin-top: 0px;
		margin-right: 0px;
		font-size: 20px;
	}
}
//! Toast styles //