.jvectormap-container {
	width: 100%;
	height: 100%;
	position: relative;
	overflow: hidden;
	-ms-touch-action: none;
	touch-action: none;
  }
  
  .jvectormap-tip {
	position: absolute;
	display: none;
	border-radius: 3px;
	background: #212529;
	color: white;
	padding: 6px;
	font-size: 11px;
	line-height: 1;
	font-weight: 700;
  
	small {
	  font-size: inherit;
	  font-weight: 400;
	}
  }
  
  .jvectormap-zoomin, .jvectormap-zoomout, .jvectormap-goback {
	position: absolute;
	left: 10px;
	border-radius: 3px;
	background: #292929;
	padding: 3px;
	color: white;
	cursor: pointer;
	line-height: 10px;
	text-align: center;
	box-sizing: content-box;
  }
  
  .jvectormap-zoomin, .jvectormap-zoomout {
	width: 10px;
	height: 10px;
  }
  
  .jvectormap-zoomin {
	top: 10px;
  }
  
  .jvectormap-zoomout {
	top: 30px;
  }
  
  .jvectormap-goback {
	bottom: 10px;
	z-index: 1000;
	padding: 6px;
  }
  
  .jvectormap-spinner {
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	background: center no-repeat url(data:image/gif;base64,);
  }
  
  .jvectormap-legright-title {
	font-weight: bold;
	font-size: 14px;
	text-align: center;
  }
  
  .jvectormap-legright-cnt {
	position: absolute;
  }
  
  .jvectormap-legright-cnt-h {
	bottom: 0;
	right: 0;
  }
  
  .jvectormap-legright-cnt-v {
	top: 0;
	right: 0;
  }
  
  .jvectormap-legright {
	background: black;
	color: white;
	border-radius: 3px;
  }
  
  .jvectormap-legright-cnt-h .jvectormap-legright {
	float: left;
	margin: 0 10px 10px 0;
	padding: 3px 3px 1px 3px;
  
	.jvectormap-legright-tick {
	  float: left;
	}
  }
  
  .jvectormap-legright-cnt-v .jvectormap-legright {
	margin: 10px 10px 0 0;
	padding: 3px;
  }
  
  .jvectormap-legright-cnt-h {
	.jvectormap-legright-tick {
	  width: 40px;
	}
  
	.jvectormap-legright-tick-sample {
	  height: 15px;
	}
  }
  
  .jvectormap-legright-cnt-v .jvectormap-legright-tick-sample {
	height: 20px;
	width: 20px;
	display: inline-block;
	vertical-align: middle;
  }
  
  .jvectormap-legright-tick-text {
	font-size: 12px;
  }
  
  .jvectormap-legright-cnt-h .jvectormap-legright-tick-text {
	text-align: center;
  }
  
  .jvectormap-legright-cnt-v .jvectormap-legright-tick-text {
	display: inline-block;
	vertical-align: middle;
	line-height: 20px;
	padding-left: 3px;
  }